.bgPanel {
  z-index: -1;
  position: fixed;
  width: 80vw;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--high-color);
  opacity: 0.3;
  box-shadow: var(--main-shadow);
}

h1 {
  margin: 4rem;
  font-size: 4rem;
  font-weight: var(--font-weight-light);
  text-transform: uppercase;
}

h2 {
  margin: 3rem;
  font-size: 3rem;
  font-weight: var(--font-weight-light);
  text-transform: uppercase;
}

main {
  margin: 0 auto 20vh auto;
}

.done {
  background: var(--line-color);
}

.spot {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 100%;
}

@media screen and (min-height: 800px) {

  main {
    max-width: 800px;
  }

}
