.notes-modal {
	height: 95vh;
}

.notes-modal .text {
	height: 55vh;
}

.notes-modal .text textarea, .notes-modal .text div {
	appearance: none;
	-moz-appearance: none;
	resize: none;
	overflow-y: scroll;
	width: 90%;
	height: 100%;
	min-height: 80%;
	padding: 4px;
	text-align: left;
	font-size: 3rem;
	border: none;
	border-bottom-left-radius: 3rem;
	border-bottom-right-radius: 3rem;
}

