.week {
    margin: 0 auto 5vh auto;
}

.no-table .actions-panel {
    display: flex;
    justify-content: space-evenly;
    width: 60%;
    margin: auto;
}

.no-table .actions-panel span {
    display: inline-block;
    margin: 0 1rem 0 0;
}

.no-table .actions-panel input {
    appearance: none;
}

.no-table .actions-panel label {
    cursor: pointer;
    z-index: 0;
}

.deg45 {
    transform: rotate(-45deg);
}

.week-date {
    border: none;
}

.week-date div {
    height: 4rem;
    font-family: var(--main-font);
    font-weight: var(--font-weight-normal);
    font-size: 2.5rem;
    line-height: 4rem;
    padding: 0;
    background: var(--base-color);
    border: none;
}

.no-table {
    border: 0;
    box-shadow: none;
}
