@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400&display=swap);
.modal-header {
	margin: 0 auto 4rem auto;
}

.modal-header .day-name {
	display: flex;
	flex-direction: column;
}

.modal-header .this-day {
	display: inline-block;
	padding: 5px;
	font-size: 16px;
	font-weight: var(--font-weight-light);
	text-transform: uppercase;
	background: var(--high-color);
	border-radius: 5px;
}

.modal-header .is-current {
	margin: -4px 0 0 0;
	font-size: 12px;
	color: var(--fade-color);
	font-weight: var(--font-weight-light);
	text-transform: uppercase;
}

nav {
    display: flex;
    justify-content: space-evenly;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: var(--high-color);
    box-shadow: 0 -5px 10px 0 rgba(0, 104, 14, 0.2);
}

a {
    color: var(--line-color);
    text-decoration: none;
}

.menu-item {
    width: 20vw;
    height: auto;
    border: none;
}

.menu-icon {
    font-size: 8rem;
    line-height: 5rem;
    font-weight: var(--font-weight-light)0;
    border-radius: 100%;
}

.menu-icon span {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    background: #c1c1c1;
    border-radius: 100%;
}

.menu-icon span.menu-icon-doit {
    background: var(--line-color);
}

.menu-text {
    font-size: 1.5rem;
    line-height: 5rem;
    text-transform: uppercase;
}

.menu-item.here .menu-icon span {
    background: #1dff75;
    border-color: #1dff75;
}

.menu-item.here .menu-icon svg {
    stroke: #1dff75;
}

.menu-item.here .menu-text {
    color: #1dff75;
}

.number-modal .options div {
    display: flex;
    justify-content: space-between;
    width: 95%;
    margin: 5px auto;
    padding: 10px;
    font-size: large;
    line-height: 5rem;
    background: #c5ddff;
}

.number-modal .options div.selected {
    background: #94bdff;
}

.number-modal .options div input {
    -webkit-appearance: none;
            appearance: none;
    -moz-appearance: textfield;
    min-width: 30px;
    max-width: 25%;
    height: 30px;
    margin: auto 0;
    font-size: large;
    background: #94bdff;
    border: 1px solid #a6adec;
    border-radius: 5px;
}

.number-modal .options div.selected input {
    background: #639fff;
}

.number-modal-bg {
    z-index: 1;
}

.main-cell {
    width: 5rem;
}

.type-cell {
    width: 100%;
    height: 100%;
    font-size: 2rem;
}

.type-cell.grey {
    color: #797979;
}

.week-spots {
    background: rgba(255,255,255,0.5);
}

.spot.grey {
    opacity: 0.4;
}

.spot.open {
    border: var(--main-border);
    background: var(--high-color);
}

.spot.closed {
    border: var(--main-border);
    background: var(--line-color);
}

.all-done .spot.closed {
    border: #1dff75;
    background: #1dff75;
}

.modal {
  position: fixed;
  z-index: 2;
  width: 95vw;
  max-height: 90vh;
  overflow-y: scroll;
  top: 0;
  left: 0;
  right: 0;
  background: var(--base-color);
  box-shadow: var(--main-shadow);
  border-bottom-left-radius: 4rem;
  border-bottom-right-radius: 4rem;
  transform: translateY(-100%);
  transition: transform 0.3s;
}

.modal.show {
  transform: translateY(0);
}

.modal-bg {
  z-index: 1;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  opacity: 0;
  transition: opacity 0.3s;
}

.modal-bg.show {
  opacity: 1;
}

.modal h2, .modal h3 {
    display: inline-block;
    margin: 4rem 1rem;
}

.modal h3 {
    font-size: large;
}

.modal .save {
    width: 55%;
    margin: 20px auto;
    padding: 0 40px;
    line-height: 6rem;
    color: #fff;
    background: #3e74c6;
    border-radius: 5px;
}

.modal .save svg {
    stroke: var(--high-color);
}

table {
    width: 100%;
    margin: 0 auto 2rem auto;
    border-collapse: collapse;
}

tr {
    height: 5rem;
}

td {
    font-size: 3rem;
    border: var(--base-border);
    box-shadow: var(--main-shadow);
}

.day {
    width: 5rem;
    background: var(--high-color);
}

.left-column {
    text-align: left;
    padding: 0 0 0 10px;
}

.week-item-actions {
    float: left;
    width: 3rem;
    height: 3rem;
    margin: auto 5px auto auto;
    font-size: 2rem;
    line-height: 1rem;
}

.small {
    font-size: 2rem;
}

.week-number-arrow-up {
    background: rgba(255, 0, 0, 0.4) url(/turtledot-x/static/media/arrowUp.d1f00b13.svg) center/cover;
}

.week-number-arrow-down {
    background: rgba(0, 255, 221, 0.4) url(/turtledot-x/static/media/arrowDown.0010f90a.svg) center/cover;
}

.all-done, .all-done .week-spots {
    color: var(--high-color);
    background: var(--line-color);
}

.scooch {
    transform: translateX(4rem);
    transition: all 0.3s ease-in-out;
}

.items-list {
	width: 80%;
	margin: 4rem auto;
    font-size: 2rem;
    border: var(--base-border);
	border-radius: 1rem;
    background: var(--high-color);
    box-shadow: var(--main-shadow);
}

.items-list-item {
    width: 100%;
    height: 3rem;
    padding: 1vh 0 2.5vh 0;
    font-size: 2rem;
    border: 0;
    border-bottom: var(--base-border);
    border-radius: 0;
}

.items-list-item:last-child {
    border-bottom: 0;
}

.add-all-items {
    width: 80%;
	margin: 1rem auto;
	font-size: 2rem;
	text-transform: uppercase;
    border-radius: 0;
}

.add-all-items.copy {
    background: #240050;
}

.notes-modal {
	height: 95vh;
}

.notes-modal .text {
	height: 55vh;
}

.notes-modal .text textarea, .notes-modal .text div {
	-webkit-appearance: none;
	        appearance: none;
	-moz-appearance: none;
	resize: none;
	overflow-y: scroll;
	width: 90%;
	height: 100%;
	min-height: 80%;
	padding: 4px;
	text-align: left;
	font-size: 3rem;
	border: none;
	border-bottom-left-radius: 3rem;
	border-bottom-right-radius: 3rem;
}

.notes-modal button {
	
}
button {
  cursor: pointer;
  width: 5rem;
  height: 5rem;
  font-family: var(--main-font);
  font-weight: var(--font-weight-normal);
  font-size: 3rem;
  padding: 0;
  background: var(--high-color);
  border: var(--base-border);
  border-radius: 100%;
  box-shadow: var(--main-shadow);
}

button.plus-sign {
  font-size: 5rem;
  font-weight: var(--font-weight-light);
  line-height: 0;
}

button.add {
  margin: 2vh auto 4vh auto;
}

button.delete, .delete {
  transform: rotate(-45deg);
}

.week {
    margin: 0 auto 5vh auto;
}

.no-table .actions-panel {
    display: flex;
    justify-content: space-evenly;
    width: 60%;
    margin: auto;
}

.no-table .actions-panel span {
    display: inline-block;
    margin: 0 1rem 0 0;
}

.no-table .actions-panel input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
}

.no-table .actions-panel label {
    cursor: pointer;
    z-index: 0;
}

.deg45 {
    transform: rotate(-45deg);
}

.week-date {
    border: none;
}

.week-date div {
    height: 4rem;
    font-family: var(--main-font);
    font-weight: var(--font-weight-normal);
    font-size: 2.5rem;
    line-height: 4rem;
    padding: 0;
    background: var(--base-color);
    border: none;
}

.no-table {
    border: 0;
    box-shadow: none;
}

html {
    --color0: #FFD951;
    --color1: #32DAFF;
    --color2: #3BF384;
    --color3: #F19FFF;
    --color4: #AE92FF;
    --color5: #FFFFFF;
    --color6: #9C9C9C;
    --color7: #FF9900;
    --color8: #5e6eff;
    --color9: #D46A83;
}

.color0 {
    background: #FFD951;
    background: var(--color0);
}

.color1 {
    background: #32DAFF;
    background: var(--color1);
}

.color2 {
    background: #3BF384;
    background: var(--color2);
}

.color3 {
    background: #F19FFF;
    background: var(--color3);
}

.color4 {
    background: #AE92FF;
    background: var(--color4);
}

.color5 {
    background: #FFFFFF;
    background: var(--color5);
}

.color6 {
    background: #9C9C9C;
    background: var(--color6);
}

.color7 {
    background: #FF9900;
    background: var(--color7);
}

.color8 {
    background: #5e6eff;
    background: var(--color8);
}

.color9 {
    background: #D46A83;
    background: var(--color9);
}

.item {
    display: flex;
    justify-content: space-between;
    margin: 0 0 2rem 0;
}

.item input {
    height: 5rem;
    font-family: var(--main-font);
    font-weight: var(--font-weight-normal);
    font-size: 3rem;
    line-height: 5rem;
    padding: 0;
    background: var(--high-color);
    border: var(--base-border);
}

.item .text {
    width: calc(33rem - 35px);
    height: 5rem;
    font-size: 3rem;
    line-height: 5rem;
    text-align: left;
    padding: 0 0 0 5px;
    background: var(--high-color);
    border: var(--base-border);
    box-shadow: var(--main-shadow);
}

.item .type {
    line-height: 32px;
}

.item .number {
    width: 8rem;
    border-radius: 5rem;
    -moz-appearance: textfield;
}

.edit-box {
    width: 100%;
    margin: 0 auto 2rem auto;
}

.edit-box-buttons {
    height: 10rem;
}

button.edit-complete {
    float: right;
    margin: 1rem;
    line-height: 36px;
}

.palette {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    background: var(--high-color);
    padding: 0.5rem;
}

.palette-square {
    width: 10.5rem;
    height: 10.5rem;
    margin: 0.5rem auto;
    border-radius: 0;
}

.info {
    position: absolute;
    top: 10px;
    right: 10px;
}

.info-modal ul {
    margin: 0 20px 40px 30px;
    padding: 0;
}

.info-modal li, .info-modal i {
    margin: 10px 0;
    padding: 0;
    font-size: 14px;
    text-align: left;
    line-height: 18px;
}

.info-modal li.indent {
    list-style-position: inside;
    list-style-type: circle;
    font-weight: var(--font-weight-light);
}

.bgPanel {
  z-index: -1;
  position: fixed;
  width: 80vw;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--high-color);
  opacity: 0.3;
  box-shadow: var(--main-shadow);
}

h1 {
  margin: 4rem;
  font-size: 4rem;
  font-weight: var(--font-weight-light);
  text-transform: uppercase;
}

h2 {
  margin: 3rem;
  font-size: 3rem;
  font-weight: var(--font-weight-light);
  text-transform: uppercase;
}

main {
  margin: 0 auto 20vh auto;
}

.done {
  background: var(--line-color);
}

.spot {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 100%;
}

@media screen and (min-height: 800px) {

  main {
    max-width: 800px;
  }

}

html {
  --base-color: #efefef;
  --high-color: #ffffff;
  --font-color: #000000;
  --line-color: #000000;
  --fade-color: #949494;
  --main-font: 'Josefin Sans', sans-serif;
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --main-border: 2px solid #000000;
  --base-border: 1px solid #efefef;
  --main-shadow: 0 20px 20px 0 rgba(0, 104, 14, 0.2);
  --main-bg-color-1: #bae1ff;
  --main-bg-color-2: #daffe9;
}

* {
  font-size: 1.6vw;
  font-family: 'Josefin Sans', sans-serif;
  font-family: var(--main-font);
  text-align: center;
  box-sizing: border-box;
  margin: auto;
}

body {
  min-height: 100vh;
  padding-bottom: 1rem;
  background: linear-gradient(to bottom right, #bae1ff, #daffe9) fixed;
  background: linear-gradient(to bottom right, var(--main-bg-color-1), var(--main-bg-color-2)) fixed;
}

@media screen and (orientation: landscape) {
  * {
    font-size: 10px;
  }
}

