.items-list {
	width: 80%;
	margin: 4rem auto;
    font-size: 2rem;
    border: var(--base-border);
	border-radius: 1rem;
    background: var(--high-color);
    box-shadow: var(--main-shadow);
}

.items-list-item {
    width: 100%;
    height: 3rem;
    padding: 1vh 0 2.5vh 0;
    font-size: 2rem;
    border: 0;
    border-bottom: var(--base-border);
    border-radius: 0;
}

.items-list-item:last-child {
    border-bottom: 0;
}

.add-all-items {
    width: 80%;
	margin: 1rem auto;
	font-size: 2rem;
	text-transform: uppercase;
    border-radius: 0;
}

.add-all-items.copy {
    background: #240050;
}
